html {
    font-size: 100%;
    box-sizing: border-box;
}

*, *::before, *::after {
    box-sizing: inherit;
}

body {
    margin: 0;
    padding: 0;
    background: var(--background);
    color: var(--label-color);
    font-family: 'Space Mono', monospace;
}

input:focus, textarea:focus, select:focus{
    outline: none;
}