header {
  text-align: center;
}

.container {
  width: 23.4375rem;
  margin: 2.5rem auto;
  padding: 1.75rem;
  border-radius: 1rem;
}
@media (min-width: 90em) {
  .container {
    width: 57.1875rem;
    margin: 5rem auto;
  }
}
.container__main {
  background: var(--white);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background: var(--background);
  color: var(--label-color);
  font-family: "Space Mono", monospace;
}

input:focus, textarea:focus, select:focus {
  outline: none;
}

:root {
  --strong-cyan: hsl(172, 67%, 45%);
  --very-dark-cyan: hsl(183, 100%, 15%);
  --dark-grayish-cyan: hsl(186, 14%, 43%);
  --dark-grayish-cyan2: hsl(184, 14%, 56%);
  --light-grayish-cyan: hsl(185, 41%, 84%);
  --light-grayish-cyan2: hsl(189, 41%, 97%);
  --white: hsl(0, 0%, 100%);
  --error: hsl(8, 35%, 53%);
  --background: var(--light-grayish-cyan);
  --label-color: var(--dark-grayish-cyan);
}

input {
  font-family: "Space Mono", monospace;
  font-size: 1.125rem;
  color: var(--dark-grayish-cyan);
}

.calculator {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 90em) {
  .calculator {
    flex-direction: row;
  }
}
.calculator fieldset {
  border: none;
}
.calculator__input {
  flex: 1;
  display: flex;
  flex-direction: column;
}
.calculator__percent {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}
.calculator__percent label {
  flex: 1;
  display: block;
}
.calculator__results {
  flex: 1;
  background: var(--very-dark-cyan);
  border-radius: 1rem;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  position: relative;
}
.calculator__results button {
  display: block;
  bottom: 2rem;
  padding: 0.625rem;
  text-transform: uppercase;
  font-weight: 500;
  background: var(--strong-cyan);
  border: none;
  border-radius: 0.625rem;
}

.radio {
  display: grid;
  grid-template-columns: 50% 50%;
  gap: 0.625rem;
}
@media (min-width: 90em) {
  .radio {
    grid-template-columns: 33% 33% 33%;
  }
}
.radio__input {
  display: none;
}
.radio__label {
  cursor: pointer;
  padding: 0.625rem;
  background: var(--very-dark-cyan);
  color: var(--white);
  font-weight: 700;
  text-align: center;
  border-radius: 0.3125rem;
}
.radio__input:checked + .radio__label {
  background: var(--strong-cyan);
  color: var(--very-dark-cyan);
}
.radio__label:hover {
  background: var(--light-grayish-cyan);
  color: var(--very-dark-cyan);
}
.radio .radio_custom {
  cursor: pointer;
  padding: 0.625rem;
  background: var(--light-grayish-cyan2);
  text-align: center;
  border-radius: 0.3125rem;
  border: 2px solid var(--white);
  font-weight: 700;
}
.radio .radio_custom:focus-within {
  border: 2px solid var(--strong-cyan);
}

.input {
  display: flex;
  align-items: center;
  padding: 0.3125rem;
  background: var(--light-grayish-cyan2);
  border-radius: 0.3125rem;
  border: 2px solid var(--white);
}
.input:focus-within {
  border: 2px solid var(--strong-cyan);
}
.input img {
  height: 18px;
  width: auto;
}
.input input {
  border: none;
  background: transparent;
  flex: 1;
  margin-left: 30px;
  font-weight: 700;
  text-align: right;
}
.input input:focus {
  border: none;
}
.input__error {
  float: right;
  color: var(--error);
}
.input__error + .input {
  border: 1px solid var(--error);
}

.results {
  position: relative;
  font-weight: 700;
}
.results section {
  display: flex;
  margin-bottom: 2.25rem;
}
.results__subtitle {
  flex: 1;
  color: var(--white);
}
.results__subtitle--per-person {
  color: var(--dark-grayish-cyan2);
  font-weight: 500;
}
.results__total {
  font-size: 1.5rem;
  color: var(--strong-cyan);
}
.results__button {
  margin-top: 1.25rem;
  font-weight: 700;
  cursor: pointer;
}
@media (min-width: 90em) {
  .results__button {
    margin-top: 2.5rem;
  }
}
.results__button:hover {
  background: var(--light-grayish-cyan);
}